.loading-error-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loading-sad-icon-container {
  margin: 1em;
}
.fa-frown,
.fa-stopwatch {
  font-size: 5em;
  color: #bdbdbd;
}
.loading-error-button {
  padding: 0.5em;
  font-size: 1em;
  cursor: pointer;
  outline: none;
  background-color: #bdbdbd;
  border-radius: 5px;
  margin-left: 0.5em;
}
.loading-delay-container {
  width: 100%;
  margin-top: 5em;
  display: flex;
  justify-content: center;
}
.loading-delay {
  height: 15px;
  width: 15px;
  border-radius: 10px;
  background-color: #2196f3;
  margin-left: 0.3em;
  margin-right: 0.3em;
  position: relative;
}
.dot1 {
  -webkit-animation: bounce 1.5s ease-in-out 0s infinite;
  animation: bounce 1.5s ease-in-out 0s infinite;
}
.dot2 {
  -webkit-animation: bounce 1.5s ease-in-out 0.15s infinite;
  animation: bounce 1.5s ease-in-out 0.15s infinite;
}
.dot3 {
  -webkit-animation: bounce 1.5s ease-in-out 0.3s infinite;
  animation: bounce 1.5s ease-in-out 0.3s infinite;
}
@keyframes bounce {
  0% {
    top: 0;
  }
  20% {
    top: -1.5em;
  }
  50% {
    top: 0;
  }
  100% {
    top: 0;
  }
}
@-webkit-keyframes bounce {
  0% {
    top: 0;
  }
  20% {
    top: -1.5em;
  }
  50% {
    top: 0;
  }
  100% {
    top: 0;
  }
}
